<script setup lang="ts">
import { useAccountStore, useTemplateStore } from '@/store'
import { computed, onBeforeUnmount, onMounted } from 'vue'
import { useI18n } from 'vue-i18n'
import { useRoute } from 'vue-router'

import debug from 'debug'

import { GET_TEMPLATE } from '../store/actions'

import IconArrowBreadCrumbs from '@/assets/icon-arrow-bread-crumbs.vue'

const logDebug = debug('ViewTemplate')

const route = useRoute()
const templateStore = useTemplateStore()
const { te, t: $t } = useI18n()

const templateName = templateStore.templateName

const currentPage = computed(() => {
  const option = route.name?.toString()
  if (te(`issue.${option}`)) {
    return $t(`issue.${option}`)
  }

  return null
})

onMounted(() => {
  logDebug(route)
  getTemplate()
})

onBeforeUnmount(() => {
  logDebug('Clear template')
  templateStore.$reset()
})

async function getTemplate() {
  if (route.params?.tid) {
    logDebug('Call GET_TEMPLATE action')
    const template = await useAccountStore()[GET_TEMPLATE]({
      tid: route.params.tid,
    })
    if (template) {
      await templateStore.setTemplate(template)
    }
  }
}
</script>

<template>
  <v-container class="view-credential">
    <v-row>
      <v-col cols="12" class="pt-7 pb-0 breadcrumbs">
        <div @click="$router.push('/Dashboard')">
          <p style="margin: 2px">
            {{ $t('dashboard.title') }}
          </p>
        </div>
        <div
          :class="{ active: !currentPage }"
          @click="currentPage && $router.go(-1)"
        >
          <IconArrowBreadCrumbs style="margin: 0px 6px 2px 6px" />
          <p style="margin: 2px 6px 2px 2px">
            {{ templateName }}
          </p>
        </div>
        <div v-if="currentPage" :class="{ active: currentPage }">
          <IconArrowBreadCrumbs style="margin: 0px 6px 2px 6px" />
          <p style="margin: 2px 6px 2px 2px">
            {{ currentPage }}
          </p>
        </div>
      </v-col>
      <v-col cols="12" class="pt-2">
        <router-view />
      </v-col>
    </v-row>
  </v-container>
</template>

<style></style>
